@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500&display=swap');

:root {
  --grey: rgb(125, 120, 120);
  --white: rgb(248, 246, 242);
  --darkgrey: rgb(116, 117, 123);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 500;
}

.wrap {
  height: 100vh;
  background-color: white;

  /* 계산기 중앙 정렬 */
  display: flex;
  justify-content: center;
  align-items: center;
}

.calculator {
  width: 350px;
  height: 520px;
  background-color: var(--white);

  border-radius: 20px;
  padding: 20px;

  box-shadow: 0px 1px 10px 1px rgba(0, 0, 0, 0.5), inset 3px 3px 3px #ffffff, inset -3px -3px 3px rgba(111, 126, 145, 0.42);

  /* 디스플레이, 버튼 컨테이너 세로 정렬 */
  display: flex;
  flex-direction: column;
}

/* 디스플레이 */
.display {
  flex: 1 0 30%;
  background-color: black;
  border-radius: 20px;

  font-size: 2.5rem;
  color: white;

  /* 숫자 왼쪽 가운데 정렬 */
  display: flex;
  /* justify-content: center; */
  justify-content: flex-end;
  align-items: center;
  padding-right: 15px;
}

