/* 버튼 컨테이너 */
.button_container {
  flex: 1 0 70%;
  /* border: 1px solid red; */
  padding-top: 10px;

  /* 버튼 5줄을 세로로 정렬 */
  display: flex;
  flex-direction: column;
}

.rows {
  flex: 1 0 10%;

  display: flex;
  justify-content: space-around;
  align-items: center;
}

/* .column {
    버튼 높이 꽉 차게
    height: 100%;

    버튼 너비 1/4씩
} */

button {
  border: none;
  border-radius: 10px;

  width: 60px;
  height: 50px;

  box-shadow: inset 2px 2px 2px #ffffff, inset -1.5px -1.5px 2px rgba(111, 126, 145, 0.42);
  cursor: pointer;

  background: none;
  font-size: 1.2rem;
}

/* 버튼 눌릴 때 */
button:active {
  background-color: rgb(233, 233, 233);
  color: rgb(176, 176, 176);
  box-shadow: inset 2px 2px 2px rgba(154, 154, 154, 0.42), inset -1.5px -1.5px 2px #ffffff;
}
